<template>
  <div>
    <el-alert
      type="warning"
      show-icon
    >
    <div slot="title">
      <span style="font-weight:bold">{{userName}}您好！</span>目前学生在线系统支持身份证号和手机号登录，为了更好的保护您的账号，请设复杂密码，切勿将收到的短信验证码泄露或转发他人。
    </div>
    </el-alert>
    <div class="btnGround">
      <el-button type="primary" @click="dialogPassword = true">修改登录密码</el-button>
      <el-button type="primary" @click="dialogPasswordQuesting = true">修改密码保护问题</el-button>
      <el-button type="primary" @click="dialogPhoto = true">修改登录手机号</el-button>
    </div>
    <el-dialog
      title="修改登录密码"
      :visible.sync="dialogPassword"
      width="30%"
      style="text-align: left;"
      :close-on-click-modal="false"
      destroy-on-close
    >
      <el-form
        label-position="top"
        size="small"
        ref="passwordForm"
        :model="passwordForm"
        :rules="rulesPassword"
      >
        <el-form-item label="原密码" prop="oldPwd">
          <el-input v-model="passwordForm.oldPwd" show-password autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="请输入新密码" prop="newPwd">
          <el-input size="small" v-model="passwordForm.newPwd" show-password autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="确认密码" prop="confirmNewPwd">
          <el-input
            size="small"
            v-model="passwordForm.confirmNewPwd"
            show-password
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item label="验证码" prop="code">
          <el-input size="large" placeholder="不区分大小写" clearable v-model="passwordForm.code">
            <template slot="append">
              <div class="ValidCode disabled-select" @click="refreshCode">
                <span
                  v-for="(item, index) in codeList"
                  :key="index"
                  :style="getStyle(item)"
                >{{item.code}}</span>
              </div>
            </template>
          </el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogPassword = false">取 消</el-button>
        <el-button type="primary" @click="updateLoginPassword">确 定</el-button>
      </span>
    </el-dialog>
    <el-dialog
      title="修改密码保护问题"
      :visible.sync="dialogPasswordQuesting"
      width="30%"
      style="text-align: left;"
      :close-on-click-modal="false"
      destroy-on-close
    >
      <el-form
        label-position="top"
        size="small"
        ref="passwordQuestiongForm"
        :model="passwordQuestiongForm"
        :rules="rulesPasswordQuesting"
      >
        <el-form-item label="请选择您的新密保问题" prop="userSecurityQuestion">
          <el-select
            v-model="passwordQuestiongForm.userSecurityQuestion"
            placeholder="请选择"
            style="width:100%"
          >
            <el-option label="我就读的小学" value="我就读的小学"></el-option>
            <el-option label="我就读的高中" value="我就读的高中"></el-option>
            <el-option label="我最喜欢的歌曲" value="我最喜欢的歌曲"></el-option>
            <el-option label="我最爱的人" value="我最爱的人"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="请输入您的问题答案" prop="userSecurityAnswer">
          <el-input v-model="passwordQuestiongForm.userSecurityAnswer" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogPasswordQuesting = false">取 消</el-button>
        <el-button type="primary" @click="updateSecurityQuestion">确 定</el-button>
      </span>
    </el-dialog>
    <el-dialog
      title="修改登录手机号"
      :visible.sync="dialogPhoto"
      width="30%"
      style="text-align: left;"
      :close-on-click-modal="false"
      destroy-on-close
      @closed="active = 1;"
    >
      <el-steps :active="active"  process-status="wait">
        <el-step title="验证密码"></el-step>
        <el-step title="修改手机号"></el-step>
      </el-steps>
      <el-form
        label-position="top"
        size="small"
        ref="photoForm"
        :model="photoForm"
        :rules="rulesPhoto"
        v-if="active == 1"
      >
        <el-form-item label="请输入您的密码" prop="verifyPassword">
          <el-input v-model="photoForm.verifyPassword" show-password autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="验证码" prop="code">
          <el-input size="large" placeholder="不区分大小写" clearable v-model="photoForm.code">
            <template slot="append">
              <div class="ValidCode disabled-select" @click="refreshCode">
                <span
                  v-for="(item, index) in codeList"
                  :key="index"
                  :style="getStyle(item)"
                >{{item.code}}</span>
              </div>
            </template>
          </el-input>
        </el-form-item>
        <el-form-item style="padding-bottom:20px;text-align: right;">
          <el-button @click="dialogPhoto = false">取 消</el-button>
          <el-button type="primary" @click="verifyPassword">下 一 步</el-button>
        </el-form-item>
      </el-form>
      <el-form
        label-position="top"
        size="small"
        ref="photoForm2"
        :model="photoForm2"
        :rules="rulesPhoto2"
        v-else
      >
        <el-form-item label="手机号" prop="newPhone">
          <el-input v-model="photoForm2.newPhone" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="验证码" prop="code">
         <div style="display:flex;align-items: center;">
          <el-input size="large" placeholder="不区分大小写" clearable v-model="photoForm2.code">
            <template slot="append">
              <div class="ValidCode disabled-select" @click="refreshCode">
                <span
                  v-for="(item, index) in codeList"
                  :key="index"
                  :style="getStyle(item)"
                >{{item.code}}</span>
              </div>
            </template>
          </el-input>
          <el-button
              type="primary"
              size="small"
              style="height:40px;margin-left:10px"
              @click="getCode(photoForm2.newPhone)"
            >发送验证码</el-button>
         </div>
        </el-form-item>
        <el-form-item label="短信验证码" prop="verifyCode">
          <el-input v-model="photoForm2.verifyCode" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item style="padding-bottom:20px;text-align: right;">
          <el-button @click="dialogPhoto = false">取 消</el-button>
          <el-button type="primary" @click="updateUserPhone">完 成</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      userName:'',
      codeList: [],
      confirmcode: "",
      active: 1,
      dialogPassword: false,
      passwordForm: {
        oldPwd: "",
        newPwd: "",
        confirmNewPwd: "",
        code: ""
      },
      rulesPassword: {
        oldPwd: [
          { required: true, message: "密码不能为空", trigger: "blur" },
          {
            min: 6,
            max: 20,
            message: "密码为6到20位数字与字母组合",
            trigger: "blur"
          }
        ],
        newPwd: [
          { required: true, message: "密码不能为空", trigger: "blur" },
          {
            min: 6,
            max: 20,
            message: "密码为6到20位数字与字母组合",
            trigger: "blur"
          }
        ],
        confirmNewPwd: [
          { required: true, message: "密码不能为空", trigger: "blur" },
          {
            min: 6,
            max: 20,
            message: "密码为6到20位数字与字母组合",
            trigger: "blur"
          },
          {
            validator: (rules, value, callback) => {
              if (this.passwordForm.newPwd !== value) {
                return callback(new Error("密码不相同"));
              }
              return callback();
            },
            trigger: "blur"
          }
        ],
        code: [
          { required: true, message: "验证码不能为空", trigger: "blur" },
          {
            validator: (rules, value, callback) => {
              if (this.confirmcode.toUpperCase() !== value.toUpperCase()) {
                return callback(new Error("验证码不相同"));
              }
              return callback();
            },
            trigger: "blur"
          }
        ]
      },

      dialogPasswordQuesting: false,
      passwordQuestiongForm: {
        userSecurityQuestion: "",
        userSecurityAnswer: ""
      },
      rulesPasswordQuesting: {
        userSecurityQuestion: [
          { required: true, message: "请选择密保问题", trigger: "blur" }
        ],
        userSecurityAnswer: [
          { required: true, message: "请回答密码问题", trigger: "blur" }
        ]
      },

      dialogPhoto: false,
      photoForm: {
        verifyPassword: "",
        code: ""
      },
      rulesPhoto: {
        verifyPassword: [
          { required: true, message: "密码不能为空", trigger: "blur" }
        ],
        code: [
          { required: true, message: "验证码不能为空", trigger: "blur" },
          {
            validator: (rules, value, callback) => {
              if (this.confirmcode.toUpperCase() !== value.toUpperCase()) {
                return callback(new Error("验证码不正确"));
              }
              return callback();
            },
            trigger: "blur"
          }
        ]
      },

      photoForm2: {
        newPhone: "",
        verifyCode: "",
        code:''
      },
      rulesPhoto2: {
        newPhone: [
          { required: true, message: "手机号不能为空", trigger: "blur" }
        ],
        verifyCode: [
          { required: true, message: "密码不能为空", trigger: "blur" }
        ],
        code: [
          { required: true, message: "验证码不能为空", trigger: "blur" },
          {
            validator: (rules, value, callback) => {
              if (this.confirmcode.toUpperCase() !== value.toUpperCase()) {
                return callback(new Error("验证码不正确"));
              }
              return callback();
            },
            trigger: "blur"
          }
        ]
      }
    };
  },
  mounted() {
    this.createdCode();
    this.userName = sessionStorage.getItem("userName");
  },
  methods: {
    refreshCode() {
      this.createdCode();
    },
    createdCode() {
      let len = 4,
        codeList = [],
        chars = "ABCDEFGHJKMNPQRSTWXYZabcdefhijkmnprstwxyz0123456789",
        charsLen = chars.length;
      // 生成
      for (let i = 0; i < len; i++) {
        let rgb = [
          Math.round(Math.random() * 220),
          Math.round(Math.random() * 240),
          Math.round(Math.random() * 200)
        ];
        codeList.push({
          code: chars.charAt(Math.floor(Math.random() * charsLen)), // 随机码
          color: `rgb(${rgb})`, // 随机颜色
          fontSize: 16 + Number(Math.floor(Math.random() * 15)) + "px", // 随机字号
          padding: `${[Math.floor(Math.random() * 10)]}px`, // 随机内边距
          transform: `rotate(${Math.floor(Math.random() * 90) -
            Math.floor(Math.random() * 90)}deg)` // 随机旋转角度
        });
      }
      // 指向
      this.codeList = codeList;
      this.confirmcode = codeList.map(item => item.code).join("");
    },
    // 动态绑定样式
    getStyle(data) {
      return `color: ${data.color}; font-size: ${data.fontSize}; padding: ${data.padding}; transform: ${data.transform}`;
    },
    //修改密码
    updateLoginPassword() {
      const userLoginAccount = sessionStorage.getItem("userLoginAccount");
      const { newPwd, oldPwd } = this.passwordForm;
      // if (!userLoginAccount) {
      //   this.$message.error("请先登录");
      //   this.$router.push({ name: "index" });
      // }
      this.$refs.passwordForm.validate(valid => {
        if (valid) {
          this.$axios
            .post("/userLogin/updateLoginPassword", {
              userLoginAccount,
              oldPwd,
              newPwd
            })
            .then(({ status }) => {
              if (status == 200) {
                this.$message({
                  message: "修改成功",
                  type: "success"
                });
                this.dialogPassword = false;
              } else if (status == 401) {
                this.$message({
                  message: "修改失败，旧密码错误",
                  type: "error"
                });
              } else if (status == 500) {
                this.$message({
                  message: "系统异常",
                  type: "warning"
                });
                this.dialogPassword = false;
              }
            })
            .catch(error => {
              console.log(error);
            })
            .finally(() => {
              this.refreshCode();
            });
        } else {
          return false;
        }
      });
    },

    //修改密保问题
    updateSecurityQuestion() {
      const userLoginAccount = sessionStorage.getItem("userLoginAccount");
      this.$refs.passwordQuestiongForm.validate(valid => {
        if (valid) {
          this.$axios
            .post("/user/updateSecurityQuestion", {
              userLoginAccount,
              ...this.passwordQuestiongForm
            })
            .then(({ status }) => {
              if (status == 200) {
                this.$message({
                  message: "修改成功",
                  type: "success"
                });
                this.dialogPasswordQuesting = false;
              } else if (status == 401) {
                this.$message({
                  message: "修改失败",
                  type: "error"
                });
              } else if (status == 500) {
                this.$message({
                  message: "系统异常",
                  type: "warning"
                });
                this.dialogPasswordQuesting = false;
              }
            })
            .catch(error => {
              console.log(error);
            })
            .finally(() => {
              this.refreshCode();
            });
        } else {
          return false;
        }
      });
    },
    //验证密码
    verifyPassword() {
      const userLoginAccount = sessionStorage.getItem("userLoginAccount");
      this.$refs.photoForm.validate(valid => {
        if (valid) {
          this.$axios
            .post("/userLogin/verifyPassword", {
              userLoginAccount,
              verifyPassword: this.photoForm.verifyPassword
            })
            .then(({ status }) => {
              if (status == 200) {
                this.$message({
                  message: "验证成功",
                  type: "success"
                });
                this.active++;
              } else if (status == 401) {
                this.$message({
                  message: "验证失败，密码错误",
                  type: "error"
                });
              } else if (status == 500) {
                this.$message({
                  message: "系统异常",
                  type: "warning"
                });
              }
            })
            .catch(error => {
              console.log(error);
            })
            .finally(() => {
              this.refreshCode();
            });
        } else {
          return false;
        }
      });
    },
    //获取验证码
    getCode(userPhone) {
      if (!userPhone) {
        this.$message({
          message: "手机号不能为空",
          type: "warning"
        });
        return;
      }
      this.$axios
        .post(
          "/getCode",
          {
            userPhone
          },
          {
            ignore: true
          }
        )
        .then(({ status }) => {
          if (status == 200) {
            this.$message({
              message: "短信已发送",
              type: "success"
            });
          } else {
            this.$message({
              message: "短信发送失败",
              type: "error"
            });
          }
        })
        .catch(error => {
          console.log(error);
        })
        .finally(() => {
          this.refreshCode();
        });
    },
    //修改手机号
    updateUserPhone() {
      const userLoginAccount = sessionStorage.getItem("userLoginAccount");
      const {newPhone,verifyCode} = this.photoForm2;
       this.$refs.photoForm2.validate(valid => {
        if (valid) {
          this.$axios
            .post("/user/updateUserPhone", {
              userLoginAccount,
              newPhone,
              verifyCode
            })
            .then(({ status }) => {
              if (status == 200) {
                this.$message({
                  message: "修改成功",
                  type: "success"
                });
                this.dialogPhoto = false;
                this.active = 1;
              } else if (status == 401) {
                this.$message({
                  message: "修改失败,验证码错误",
                  type: "error"
                });
              } else if (status == 402) {
                this.$message({
                  message: "修改失败，新旧手机号相同",
                  type: "warning"
                });
              }
            })
            .catch(error => {
              console.log(error);
            })
            .finally(() => {
              this.refreshCode();
            });
        } else {
          return false;
        }
      });
    }
  }
};
</script>
<style lang="scss" scoped>
.btnGround {
  margin-top: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  .el-alert {
    height: 40px;
  }
  .el-button {
    height: 60px;
    font-size: 20px;
    width: 600px;
    margin: 5px 0;
  }
  .el-button + .el-button {
    margin-left: 0px;
  }
}

.ValidCode {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  height: 39px;
  width: 110px;
  background: rgb(214, 214, 214);
  span {
    display: inline-block;
  }
}
/deep/ .el-dialog__body {
  padding: 10px 20px 0 20px;
}
</style>